*{
  box-sizing: border-box;
}

html{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

body{
  margin: 0;
}
 
.people{
  gap: 30px 40px;
  display: grid;
  padding: 30px 35px;

  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(4, 200px);
  grid-template-rows: 1fr 1fr;

  @media (max-width: 999px) {
    grid-template-columns: repeat(2, 200px);
  }

  @media (min-width: 1000px) {
    grid-template-columns: repeat(4, 200px);
  }
}

img{
  border-radius: 200px;
  width: 200px;
  height: 200px;
  position: relative;
}


.rank{
  display: flex;
  font-size: 19px;
  justify-content: center;
  position: absolute;
  align-items: center;
  font-weight: bold;
  color: rgba(139, 0, 0, 1);
  background-color: rgba(255, 255, 255, 1);

  border-radius: 200px ;
}

.details{
  margin-top: 3px;
  font-weight: bold;
  color: rgba(47, 79, 79, 1);
  text-align: center;
}

.extra{
 overflow: hidden;
 position: relative;
}
